/* 전체 채팅 레이아웃 */
.chat-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
    font-family: Arial, sans-serif;
  }
  
  /* 상단 이름바 */
  .chat-header {
    background-color: #fdd835;
    padding: 15px;
    text-align: center;
    font-size: 20px;
    font-weight: bold;
  }
  
  /* 채팅 메시지 영역 */
  .chat-body {
    flex: 1;
    padding: 15px;
    overflow-y: auto;
    background-color: #f1f1f1;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  /* 말풍선 공통 */
  .chat-message {
    max-width: 60%;
    padding: 10px 14px;
    border-radius: 15px;
    font-size: 14px;
    line-height: 1.4;
  }
  
  /* 내 메시지 말풍선 */
  .chat-message.me {
    background-color: #dcf8c6; /* 연두색 */
    align-self: flex-end;
  }
  
  /* 상대 메시지 말풍선 */
  .chat-message.other {
    background-color: #ffffff; /* 흰색 */
    align-self: flex-start;
  }
  
  /* 하단 입력창 */
  .chat-footer {
    display: flex;
    padding: 10px;
    border-top: 1px solid #ccc;
    background-color: #fff;
  }
  
  .chat-footer input {
    flex: 1;
    padding: 10px;
    font-size: 14px;
    border: 1px solid #ddd;
    border-radius: 4px;
    margin-right: 10px;
  }
  
  .chat-footer button {
    padding: 10px 16px;
    background-color: #fdd835;
    border: none;
    border-radius: 4px;
    font-weight: bold;
    cursor: pointer;
  }
  /*신고 버튼 */
.report-btn, .block-btn {
  margin-left: 10px;
  padding: 5px 10px;
  font-size: 0.8em;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
/* 신고*/
.report-btn {
  background-color: #ff9800;
  color: white;
}
/*차단*/
.block-btn {
  background-color: #f44336;
  color: white;
}